import { useState, useEffect } from 'react';
import { contactsArray } from '../../services/mockups'
import Icons from '../../assets/svgs'
import './styles.css';

import {
  GetContactsADDI,
  ValidPersonalData,
  DoesNotHaveJudicialRecords,
  HasSatisfactoryScore
} from '../../services/validations';

function ContactsTable() {

  const [contactsDataArray, setContactsDataArray] = useState([{loading: true}, {loading: true}, {loading: true}, {loading: true}]);
  useEffect(async () => {
    const res = await GetContactsADDI();
    setContactsDataArray(res);
  }, []);

  const checkContact = async contact => {
    const bufferContactsArray = contactsDataArray.map(el => {
      if (el.id === contact.id) {
        contact.checked = !contact.checked;
      }
      return el;
    });
    setContactsDataArray(bufferContactsArray);
        
    const allChecked = bufferContactsArray.every(contact => contact.checked);
    setAllContactsChecked(allChecked);
  }

  const [allContactsChecked, setAllContactsChecked] = useState(false);
  const toggleAllContacts = () => {
    const bufferContactsArray = contactsDataArray.map(el => {
      el.checked = !allContactsChecked;
      return el;
    });
    setAllContactsChecked(!allContactsChecked);
    setContactsDataArray(bufferContactsArray);
  }

  const getModifiedArray = (contact, lifeCycle, status) => {
    return contactsDataArray.map(el => {
      if (el.nationalIdentification === contact.nationalIdentification) {
        el.loading = false;
        el.lifeCycle = lifeCycle;
        el[status] = true;
      }
      return el;
    });
  }

  const necessaryScore = 60;
  const performValidationsAndProspect = async contact => {
    const loadingContactsArray = contactsDataArray.map(el => {
      if (contact.nationalIdentification === el.nationalIdentification) {
        el.checked = false;
        el.loading = true;
      }
      return el
    });
    setContactsDataArray(loadingContactsArray);

    const concurrentPromises = await Promise.all([ValidPersonalData(contact), DoesNotHaveJudicialRecords(contact)])
    const passedConcurrentPromises = concurrentPromises.every(promise => promise);

    if (passedConcurrentPromises) {
      const convertToProspect = await HasSatisfactoryScore(contact, necessaryScore);
      if (convertToProspect) {
        console.log(`Congratulations, the Lead ${contact.firstName} passed through all validations and it is a Prospect now!`)
        setContactsDataArray(getModifiedArray(contact, 'Prospect', 'prospect'));
      } else {
        console.warn(`Info: The Lead ${contact.firstName} still needs to improve its score`)
        setContactsDataArray(getModifiedArray(contact, 'Lead', 'warning'));
      }
    } else {
      console.error(`Error! The Lead ${contact.firstName} didn't pass the Personal Validation`)
      setContactsDataArray(getModifiedArray(contact, 'Lead', 'danger'));
    }
  }
  const prospectSelected = async () => {
    contactsDataArray.map(async contact => {
      if (contact.checked) {
        performValidationsAndProspect(contact);
      }
    });
    setAllContactsChecked(false);
  }

  const tableColumns = [
    <div className="text-center pointer ml-16" onClick={() => toggleAllContacts && toggleAllContacts()}>
      { allContactsChecked
        ? Icons.CheckboxSelected
        : Icons.CheckboxRounded
      }
    </div>,
    'Full Name',
    'National Identification',
    'Life Cycle',
    'Email',
    'Birthdate',
  ];

  const getFullName = contact => contact.firstName ? `${contact.firstName} ${contact.lastName}` : ''
  const getFormattedIdentification = identification => identification?.toString().replace(/(\d{3})(\d{3})(\d{3})(\d{2})/, "$1.$2.$3-$4")

  return (
    <section>
      <div className="above-table">
        {contactsDataArray.some(el => !!el.checked) && (
          <button className="btn-secondary" onClick={() => prospectSelected()}>
            {`Prospect ${contactsDataArray.reduce((sum, next) => sum + !!next.checked, 0)} Leads`}
          </button>
        )}
      </div>
      <table>
        <thead>
          <tr>
            { tableColumns.map(column => <th className="text-left" key={column}> {column} </th>)}
          </tr>
        </thead>
        <tbody>
          { contactsDataArray && contactsDataArray.map(contact => (
            contact.loading
              ? <tr className="empty-state">
                <td><div></div></td>
                <td><div></div></td>
                <td><div></div></td>
                <td><div></div></td>
                <td><div></div></td>
                <td><div></div></td>
                <td><div></div></td>
              </tr>
              : <tr className={`${contact.checked ? ' row-checked ' : ''}`}
                  key={contact.nationalIdentification}>
                <td onClick={() => checkContact(contact)}
                    className={`flex checkbox-column ${contact.checked ? 'checked' : ''}`}>
                  <img alt={contact.firstName} className="photo" src={contact.photoUrl} />
                  <div className="icon">
                    { contact.checked
                      ? Icons.CheckboxSelected
                      : Icons.CheckboxRounded
                    }
                  </div>
                </td>
                <td>{getFullName(contact)}</td>
                <td>
                  {getFormattedIdentification(contact.nationalIdentification)}
                </td>
                <td className="">
                  { !contact.warning && !contact.danger && !contact.prospect && (
                    <div className={'flex align-center'}>
                      <span className="mr-10">{contact.lifeCycle}</span>
                    </div>
                  )}
                  { contact.prospect && (
                    <div className={'flex align-center text-primary bold'}>
                      { Icons.Star }
                      <span className="mr-10">{contact.lifeCycle}</span>
                    </div>
                  )}
                  { contact.warning && (
                    <div className={'flex align-center text-warning bold'}>
                      { Icons.Funnel }
                      <span className="mr-10">{contact.lifeCycle}</span>
                    </div>
                  )}
                  { contact.danger && (
                    <div className={'flex align-center text-danger bold'}>
                      { Icons.Warning }
                      <span className="mr-10">{contact.lifeCycle}</span>
                    </div>
                  )}
                </td>
                <td>{contact.email}</td>
                <td>{new Date(contact.birthdate).toLocaleDateString()}</td>
                <td>
                  <button
                    disabled={contact.prospect}
                    className={`btn-secondary`}
                    onClick={() => performValidationsAndProspect(contact)}>
                      Prospect
                  </button>
                </td>
              </tr>
          ))}
        </tbody>

        <tfoot>
          <tr>
          </tr>
        </tfoot>
      </table>
    </section>
  )
}

export default ContactsTable;
