export const GetContactsADDI = async () => {
  const res = await fetch(`https://my-json-server.typicode.com/murilloves/fake-api-addi/contactsArray`)
  return await res.json();
}

export const GetFederalInformationByNationalID = async (nID) => {
  const res = await fetch(`https://my-json-server.typicode.com/murilloves/fake-api-addi/federalGovernmentData?nationalIdentification=${nID}`)
  return await res.json();
}

export const ValidPersonalData = async contact => {
  const PhysicPerson = (await GetFederalInformationByNationalID(contact.nationalIdentification))[0];

  return await new Promise(resolve => {
    if (!PhysicPerson) {
      resolve(null)
      return;
    }
    const PhysicPersonBirthdate = new Date(PhysicPerson.birthdate);
    const ContactBirthdate = new Date(contact.birthdate);
  
    const validFirstName = contact.firstName.toLowerCase().indexOf(PhysicPerson.firstName.toLowerCase()) >= 0;
    const validLastName = contact.lastName.toLowerCase().indexOf(PhysicPerson.lastName.toLowerCase()) >= 0;
    const validBirthDate = (
      ContactBirthdate.getFullYear() === PhysicPersonBirthdate.getFullYear()
    );

    resolve(validFirstName && validLastName && validBirthDate);
  })
}

export const DoesNotHaveJudicialRecords = async contact => {
  const PhysicPerson = (await GetFederalInformationByNationalID(contact.nationalIdentification))[0];

  return await new Promise(resolve => {
    if (!PhysicPerson) {
      resolve(null)
      return;
    }

    const doesntHaveRecords = PhysicPerson.judicialRecords.length < 1;
    resolve(doesntHaveRecords)
  })
}

export const HasSatisfactoryScore = async (contact, necessaryScore) => {
  const PhysicPerson = (await GetFederalInformationByNationalID(contact.nationalIdentification))[0];

  return await new Promise(resolve => {
    if (!PhysicPerson) {
      resolve(null)
      return;
    }

    const passedScoreTest = PhysicPerson.score > necessaryScore;
    resolve(passedScoreTest)
  });
}
